import * as React from 'react';
import { Form, Field, FormikProps, FieldProps, withFormik, FormikBag } from 'formik';
import { RouteComponentProps } from '@reach/router';
import FormInput from '../../../common/components/form/FormInput';
import { formFields, validation, FormValues } from '../components/fields/generalFormFields';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { createUser } from '../../user/forces/actions';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import FormAlert from '../../../common/components/form/FormAlert';
import { parseQueryString } from '../../../utils/url.utils';
import FormInputError from '../../../common/components/form/FormInputError';
import { BodyBolderText } from '../../../common/components/texts';
import config from '../../../config';
import styles from './RegistrationGeneralForm.module.css';

interface OwnProps {
  onUserCreated: () => void;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & RouteComponentProps;
type PropsWithFormik = Props & React.HTMLAttributes<HTMLElement> & FormikProps<FormValues>;

class RegistrationGeneralForm extends React.Component<PropsWithFormik> {
  static getParsedQueryString(props: Props) {
    return parseQueryString(props.location ? props.location.search : '');
  }

  render() {
    const parsedQueryString = RegistrationGeneralForm.getParsedQueryString(this.props);
    return (
      <Form>
        <BodyBolderText className={styles.explanationText}>
          Bli medlem av olio, det er gratis! Som medlem tjener du{' '}
          {config.bookingPointsEarningsPercentage}% olio-poeng p alle behandlinger. 1 olio-poeng =
          1 kr. Du vil ogs motta {config.signupCreditAward} olio-poeng nr du melder deg inn.
        </BodyBolderText>
        {formFields.map(formField => (
          <React.Fragment key={formField.name}>
            <Field
              name={formField.name}
              render={({ field }: FieldProps<FormValues>) => {
                const shouldDisableField = parsedQueryString[field.name] != null;
                return (
                  <FormInput
                    {...field}
                    styleType="secondary"
                    type={formField.type}
                    required={formField.required}
                    placeholder={formField.placeholder}
                    disabled={shouldDisableField}
                  />
                );
              }}
            />
            <FormInputError name={formField.name} component="div" />
          </React.Fragment>
        ))}
        <FormAlert error={this.props.status} />
        {this.props.isSubmitting ? (
          <LoadingIndicator />
        ) : (
          <CallToActionButton size="large" type="submit" disabled={this.props.isSubmitting}>
            Neste
          </CallToActionButton>
        )}
      </Form>
    );
  }
}

const formOptions = {
  mapPropsToValues: (props: Props) => {
    const defaultValues = {
      name: '',
      email: '',
      phoneNumber: '',
      password: '',
      referralCode: '',
    };
    const queryParamsValues = RegistrationGeneralForm.getParsedQueryString(props);
    return { ...defaultValues, ...queryParamsValues };
  },
  handleSubmit: async (values: FormValues, formikBag: FormikBag<Props, FormValues>) => {
    try {
      await formikBag.props.createUser(values);
      formikBag.props.onUserCreated();
    } catch (err) {
      formikBag.setSubmitting(false);
      formikBag.setStatus(err.message || 'Noe gikk galt!');
    }
  },
  validationSchema: validation,
};
const FormikRegisterPage = withFormik<Props, FormValues>(formOptions)(RegistrationGeneralForm);

const mapStateToProps = (state: RootState) => ({});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      createUser,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(FormikRegisterPage);
