import React from 'react';
import classNames from 'classnames';
import styles from './StepProgressBar.module.css';

interface Props<T> {
  steps: Array<T>;
  currentStep: T;
}

const StepProgressBar: <T>(props: Props<T>) => React.ReactElement<Props<T>> = ({
  steps,
  currentStep,
}) => {
  return (
    <ul className={styles.progressbar}>
      {steps.map((step, i) => {
        const isActive = step === currentStep;
        return (
          <li key={i} className={classNames(styles.listItem, { [styles.active]: isActive })} />
        );
      })}
    </ul>
  );
};

export default StepProgressBar;
