import * as Yup from 'yup';

export interface FormValues {
  name: string;
  email: string;
  phoneNumber: string;
  password: string;
  referralCode: string;
}

export const formFields = [
  {
    name: 'name',
    type: 'text',
    placeholder: 'Fullt navn',
    error: 'Skriv',
    required: true,
  },
  {
    name: 'email',
    type: 'text',
    placeholder: 'Epost',
    error: 'Skriv',
    required: true,
  },
  {
    name: 'phoneNumber',
    type: 'tel',
    placeholder: 'Telefonnummer',
    error: 'Skriv',
    required: true,
  },
  {
    name: 'password',
    type: 'password',
    placeholder: 'Passord',
    error: 'Skriv',
    required: true,
  },
  {
    name: 'referralCode',
    type: 'text',
    placeholder: 'Vervekode',
    required: false,
  },
];

export const validation = Yup.object().shape<FormValues>({
  name: Yup.string()
    .min(2, 'For kort!')
    .max(50, 'For lang!')
    .required('Pkrevd  fylle ut navn'),
  email: Yup.string()
    .email('Invalid email')
    .required('Pkrevd  fylle ut epost'),
  phoneNumber: Yup.string()
    .min(8, 'For kort!')
    .max(12, 'For lang!'),
  password: Yup.string()
    .min(2, 'For kort!')
    .max(50, 'For lang!'),
  referralCode: Yup.string(),
});
