import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { setPaymentInformation } from '../../user/forces/actions';
import { BodyBolderText } from '../../../common/components/texts';
import PaymentContainer from '../../user/containers/PaymentContainer';
import styles from './RegistrationBillingForm.module.css';

interface OwnProps {
  onSkipButtonClicked: () => void;
  onPaymentSubmitted: () => void;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

interface State {
  error: Error | null;
  isLoading: boolean;
}

class RegistrationBillingForm extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isLoading: false,
      error: null,
    };
  }

  renderHeader() {
    return (
      <BodyBolderText className={styles.text}>
         vre medlem hos oss er gratis. Kortet ditt vil bli belastet kun nr du har booket time.
      </BodyBolderText>
    );
  }

  render() {
    return (
      <PaymentContainer
        header={this.renderHeader()}
        onSkipButtonClicked={() => this.props.onSkipButtonClicked()}
        onPaymentSubmitted={() => this.props.onPaymentSubmitted()}
        onSubmitCardToken={async cardToken => this.props.setPaymentInformation({ cardToken })}
      />
    );
  }
}

const mapStateToProps = (state: RootState) => ({});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setPaymentInformation,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(RegistrationBillingForm);
