import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import * as userSelectors from '../../user/forces/selectors';
import { createUser, setPaymentInformation } from '../../user/forces/actions';

import UserPageLayout from '../../../common/components/layout/UserPageLayout';
import RegistrationGeneralForm from '../containers/RegistrationGeneralForm';
import RegistrationSummary from '../components/RegistrationSummary';
import RegistrationBillingForm from '../containers/RegistrationBillingForm';
import StepProgressbar from '../../../common/components/progress/StepProgressBar';
import { RegistrationStep } from '../../user/forces/types';
import { assertUnreachable } from '../../../utils/ts.utils';
import { RouteComponentProps } from '@reach/router';
import styles from './RegisterPage.module.css';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

interface State {
  referralCode: string | null;
  currentStep: RegistrationStep;
}

class RegisterPage extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      referralCode: null,
      currentStep: RegistrationStep.GeneralForm,
    };
  }

  renderCurrentStep() {
    const { location } = this.props;
    const currentStep = this.state.currentStep;
    switch (currentStep) {
      case RegistrationStep.GeneralForm:
        return (
          <RegistrationGeneralForm
            location={location}
            onUserCreated={() => this.setState({ currentStep: RegistrationStep.Billing })}
          />
        );
      case RegistrationStep.Billing: {
        return (
          <RegistrationBillingForm
            onSkipButtonClicked={() => this.setState({ currentStep: RegistrationStep.Summary })}
            onPaymentSubmitted={() => this.setState({ currentStep: RegistrationStep.Summary })}
          />
        );
      }
      case RegistrationStep.Summary:
        return <RegistrationSummary />;
      default:
        return assertUnreachable(currentStep);
    }
  }

  render() {
    const currentStep = this.state.currentStep;
    return (
      <UserPageLayout title="Join the freshness" className={styles.container} {...this.props}>
        <StepProgressbar
          steps={[RegistrationStep.GeneralForm, RegistrationStep.Billing, RegistrationStep.Summary]}
          currentStep={currentStep}
        />
        {this.renderCurrentStep()}
      </UserPageLayout>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  hasAddedPaymentInfo: userSelectors.hasAddedPaymentInfo(state),
  token: userSelectors.getToken(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      registerStepOne: createUser,
      registerStepTwo: setPaymentInformation,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(RegisterPage);
